<template>
  <div>
    <gs-button
      type="primary"
      @click="openAddMemberModal"
    >添加成员</gs-button>
    <gs-button
      type="primary"
      @click="removeMember"
    >移除成员</gs-button>
    <gs-modal
      v-model="visible"
      title="添加成员"
      width="620px"
      @visible-change="visibleChange"
      @confirm="addMember"
    >
      <div class="add-member-modal">
        <gs-search
          v-model="keyword"
          class="search"
          @search="handleSearch"
          @clear="handleSearch"
        >
          <template slot="prepend">
            <gs-select
              v-model="roleFilter"
              @change="roleChange"
            >
              <gs-option
                v-for="item in roleOptions"
                :key="item.roleId"
                :label="item.roleName"
                :value="item.roleId"
                :title="item.roleName"
              />
            </gs-select>
          </template>
        </gs-search>
        <gs-modal-select
          v-model="selected"
          class="searchable-modal-select"
          flatten
        >
          <gs-option
            v-for="item in memberOptions"
            :key="item.userId"
            :label="item.username"
            :value="item.userId"
            :disabled="!!item.status"
          >
            <span>{{ item.username }}</span>
            <span class="role-name">({{ item.roleName }})</span>
          </gs-option>
        </gs-modal-select>
      </div>
      <gs-loading
        :is-show="loading"
      />
    </gs-modal>
  </div>
</template>
<script>
import * as roleService from 'src/service/auth/role';
export default {
  props: {
    roleId: {
      type: String,
      required: true,
      default: ''
    }
  },
  data() {
    return {
      visible: false,
      roleOptions: [],
      roleFilter: 'all',
      memberOptions: [],
      keyword: '',
      selected: [],
      loading: false
    };
  },
  methods: {
    openAddMemberModal() {
      this.visible = true;
    },
    removeMember() {
      this.$emit('removeMember');
    },
    visibleChange(val) {
      if (val) {
        this.loading = true;
        this.roleFilter = 'all';
        this.selected = [];
        roleService.getRoleList({limit: -1, query: ''}).then(data => {
          data.lists = [
            {
              roleName: '全部',
              roleId: 'all'
            },
            ...data.lists
          ];
          const index = data.lists.findIndex(item => item.roleId === this.roleId);
          if (index !== -1) data.lists.splice(index, 1);
          this.roleOptions = data.lists.filter(i => i.roleId !== '1');
        });
        roleService.getAllMemberList(this.roleId, {limit: -1, query: this.keyword}).then(data => {
          this.memberOptions = data.lists;
          this.loading = false;
        });
      }
    },
    addMember() {
      if (this.selected && this.selected.length) {
        this.$emit('addMember', this.selected);
        this.visible = false;
      } else {
        this.$Message.warning(`请选择成员`);
      }
    },
    handleSearch() {
      if (this.roleFilter === 'all') {
        roleService.getAllMemberList(this.roleId, {limit: -1, query: this.keyword}).then(data => {
          this.memberOptions = data.lists;
        });
      } else {
        roleService.getMemberList(this.roleFilter, {limit: -1, query: this.keyword}).then(data => {
          this.memberOptions = data.lists;
        });
      }
    },
    roleChange() {
      this.keyword = '';
      if (this.roleFilter === 'all') {
        roleService.getAllMemberList(this.roleId, {limit: -1, query: ''}).then(data => {
          this.memberOptions = data.lists;
        });
      } else {
        roleService.getMemberList(this.roleFilter, {limit: -1, query: ''}).then(data => {
          this.memberOptions = data.lists;
        });
      }
    }
  }
};
</script>




