<template>
  <div class="role-detail">
    <gs-tabs
      v-model="activeName"
      type="content-tab"
    >
      <gs-tab-pane
        label="成员"
        name="member"
      >
        <member />
      </gs-tab-pane>
      <gs-tab-pane
        label="权限"
        name="authorize"
      >
        <authorize />
      </gs-tab-pane>
    </gs-tabs>
  </div>
</template>
<script>
import authorize from './authorize';
import member from './member';
export default {
  components: {
    authorize,
    member
  },
  data() {
    return {
      activeName: 'member'
    };
  }
};
</script>



