<template>
  <div
    v-loading="loading"
    class="authorize"
  >
    <div class="header">
      <div class="title">
        <span class="label">权限方案:</span>
        <span v-if="!edit">{{ privilege.privilegeName }}</span>
        <gs-select
          v-else
          v-model="privilegeId"
          class="doc-select"
          popper-class="searchable-select"
          @change="changePrivilegeOption"
        >
          <gs-option
            v-for="item in privilegesOptions"
            :key="item.privilegeId"
            :label="item.privilegeName"
            :value="item.privilegeId"
          />
        </gs-select>
      </div>
      <div class="operation-box">
        <gs-button
          v-show="allowEdit && !edit"
          type="text-primary"
          @click="editPrivilege"
        >编辑</gs-button>
        <gs-button
          v-show="edit"
          type="text-primary"
          @click="changePrivilege"
        >确定</gs-button>
        <gs-button
          v-show="edit"
          type="text-primary"
          @click="cancel"
        >取消</gs-button>
      </div>
    </div>
    <div class="wrap">
      <div class="left">
        <div class="authorize-module">
          <div class="title">授权模块</div>
          <p
            v-for="item in privilege.modules.filter(i => i.type) || []"
            :key="item.moduleId"
          >
            <i class="gs-icon-check" />{{ item.moduleName }}
          </p>
        </div>
        <div class="business-obj-ope">
          <div class="title">业务对象操作</div>
          <p
            v-for="item in privilege.modules.filter(i => !i.type) || []"
            :key="item.moduleId"
          >
            <i class="gs-icon-check" />{{ item.moduleName }}
          </p>
        </div>
      </div>
      <div class="right">
        <div class="title">授权范围</div>
        <div class="wrapper">
          <div
            v-show="privilege.modules.some(item =>item.moduleId === 1)"
            class="technical"
          >
            <div class="title">技术目录</div>
            <gs-tree
              ref="technicalTree"
              :data="technicalCategories"
              :props="{
                label: 'name',
                className: 'hide'
              }"
              :render-content="renderTree"
              node-key="guid"
            />
          </div>
          <div
            v-show="privilege.modules.some(item => [2, 5].includes(item.moduleId))"
            class="bussiness"
          >
            <div class="title">业务目录</div>
            <gs-tree
              ref="businessTree"
              :data="businessCategories"
              :props="{
                label: 'name'
              }"
              :render-content="renderTree"
              node-key="guid"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  list2tree,
  sortTreeList,
  deleteInTree
} from 'src/utils';
import * as service from 'src/service/auth/role';
import * as authService from 'src/service/auth/auth';
export default {
  data() {
    return {
      privilege: {
        privilegeName: '',
        modules: []
      },
      storePrivilege: {},
      roleId: '',
      technicalCategories: [],
      businessCategories: [],
      edit: false,
      privilegesOptions: [],
      privilegeId: '',
      businessCheck: [],
      technicalCheck: [],
      loading: false,
      allowEdit: false
    };
  },
  created() {
    this.roleId = this.$route.params.id;
    this.fetchRolePrivilege();
  },
  methods: {
    fetchRolePrivilege() {
      this.loading = true;
      service.getPrivileges({
        limit: -1,
        query: ''
      }).then(data => {
        this.privilegesOptions = data.lists;
      });
      service.getRolePrivilege(this.roleId).then(data => {
        this.allowEdit = !!(data.edit);
        if (data.technicalCategories && data.technicalCategories.length) {
          data.technicalCategories.map(item => {
            item.disabled = !item.show;
            item.status && this.technicalCheck.push(item.guid);
            return item;
          });
          const technicalUnsortCategories = list2tree(data.technicalCategories, 'guid', 'parentCategoryGuid');
          const technicalCategories = sortTreeList(technicalUnsortCategories, 'guid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
          this.technicalCategories = deleteInTree(technicalCategories, true, 'hide', false);
        }
        if (data.businessCategories && data.businessCategories.length) {
          data.businessCategories.map(item => {
            item.disabled = !item.show;
            item.status && this.businessCheck.push(item.guid);
            return item;
          });
          const businessUnsortCategories = list2tree(data.businessCategories, 'guid', 'parentCategoryGuid');
          const businessCategories = sortTreeList(businessUnsortCategories, 'guid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
          this.businessCategories = deleteInTree(businessCategories, true, 'hide', false); ;
        }
        this.privilegeId = data.privilege.privilegeId;
        this.privilege = data.privilege;
        this.storePrivilege = JSON.parse(JSON.stringify(data.privilege));
        this.loading = false;
      });
    },
    renderTree(h, { node, data }) {
      const { edit } = this;
      return (
        data.hide
          ? <div></div>
          : <span>
            {(!edit)
              ? (data.status ? <i class="gs-icon-check"></i> : null)
              : <gs-checkbox
                value={!!data.status}
                disabled={!data.show}
                on-change={this.categoryChange.bind(this, data)}
              ></gs-checkbox>}
            <span>{data.name}</span>
          </span>
      );
    },
    filterNode(tree) {
      function travel(data) {
        data = data.filter(i => {
          if (i.children) {
            i.children = travel(i.children);
          };
          return !i.hide;
        });
        return data;
      }
      return travel(tree);
    },
    categoryChange(data) {
      const setChildrenStatus = (data, status) => {
        if (data.children) {
          data.children.map(item => {
            if (item.children) {
              item.status = status;
              return setChildrenStatus(item, status);
            } else {
              item.status = status;
              return item;
            }
          });
        }
      };
      data.status = data.status === 1 ? 0 : 1;
      setChildrenStatus(data, data.status);
    },
    changePrivilege() {
      const params = {};
      params.privilege = {
        privilegeId: this.privilegeId,
        modules: this.privilege.modules
      };
      // const technicalCategories = this.$refs.technicalTree.getCheckedKeys(true);
      // const businessCategories = this.$refs.businessTree.getCheckedKeys(true);
      const getCheckedKeys = (categories) => {
        function deepFlatten(arr) {
          const flatten = (arr) => [].concat(...arr);
          return flatten(arr.map(x => {
            if (Array.isArray(x.children) && x.status === 0) {
              return [...x, ...deepFlatten(x.children)];
            } else {
              return x;
            }
          }));
        }
        return deepFlatten(categories)
          .filter(item => item.status === 1)
          .map(item => item.guid);
      };
      const technicalCategories = getCheckedKeys(this.technicalCategories);
      const businessCategories = getCheckedKeys(this.businessCategories);
      if (technicalCategories && technicalCategories.length) {
        params.technicalCategories = technicalCategories.map(item => {
          return {
            guid: item,
            status: 1
          };
        });
      }
      if (businessCategories && businessCategories.length) {
        params.businessCategories = businessCategories.map(item => {
          return {
            guid: item,
            status: 1
          };
        });
      }
      service.changePrivilege({
        params,
        roleId: this.roleId
      }).then(data => {
        this.edit = false;
        this.$Message.success('修改成功');
        this.fetchRolePrivilege();
      });
    },
    changePrivilegeOption(value) {
      authService.getPrivilegeDetail(value).then(data => {
        this.privilege.modules = data.modules;
      });
    },
    cancel() {
      this.edit = false;
      this.privilege = JSON.parse(JSON.stringify(this.storePrivilege));
      this.privilegeId = this.privilege.privilegeId;
      this.technicalCategories = JSON.parse(this.copyTechnicalTree);
      this.businessCategories = JSON.parse(this.copyBusinessTree);
    },
    editPrivilege() {
      this.edit = true;
      // this.$refs.technicalTree.setCheckedKeys(this.technicalCheck);
      // this.$refs.businessTree.setCheckedKeys(this.businessCheck);
      this.copyTechnicalTree = JSON.stringify(this.technicalCategories);
      this.copyBusinessTree = JSON.stringify(this.businessCategories);
    }
  }
};
</script>



