import http from 'src/utils/http';

export function getPrivilegeList(params) {
  return http.post(`/privilege/privileges`, params);
}
export function deletePrivilege(privilegeId) {
  return http.delete(`/privilege/${privilegeId}`);
}
export function getPrivilegeDetail(privilegeId) {
  return http.get(`/privilege/${privilegeId}`);
}
export function getModules() {
  return http.get(`/privilege/modules`);
}
export function createPrivilege(form) {
  return http.post(`/privilege`, form);
}
export function editPrivilege(privilegeId, form) {
  return http.put(`/privilege/${privilegeId}`, form);
}
export function getRoleList(params) {
  return http.post(`/privilege/roles`, params);
}



