<template>
  <div class="member">
    <div
      :class="{ 'flex-end': roleId === '1' }"
      class="search-box"
    >
      <add-member
        v-if="roleId !== '1'"
        :role-id="roleId"
        @removeMember="removeMember"
        @addMember="addMember"
      />
      <gs-search
        v-model="keyword"
        placeholder="请输入用户名或帐号"
        @clear="fetchMemberList(1)"
        @search="fetchMemberList"
      />
    </div>
    <gs-table
      v-loading="loading"
      :data="tableData"
      :pagination="pagination"
      style="width: 100%"
      paging
      @size-change="sizeChange"
      @page-change="pageChange"
      @selection-change="selectionChange"
    >
      <gs-table-column
        type="selection"
        fixed
      />
      <gs-table-column
        prop="account"
        label="帐号"
        show-overflow-tooltip
      />
      <gs-table-column
        prop="username"
        label="用户名"
        show-overflow-tooltip
      />
      <gs-table-column
        prop="roleName"
        label="角色"
      >
        <template slot-scope="scope">
          <gs-table-tags
            :tags="scope.row.roles"
            :show-count="2"
          />
        </template>
      </gs-table-column>
    </gs-table>
  </div>
</template>
<script>
import './style';
import addMember from './add-member';
import * as service from 'src/service/auth/role';
export default {
  components: {
    addMember
  },
  data() {
    return {
      keyword: '',
      tableData: [
      ],
      pagination: {
        total: 0,
        pageSize: 10,
        pageSizes: [10, 20, 30],
        current: 1
      },
      loading: false,
      selection: [],
      roleId: ''
    };
  },
  computed: {

  },

  created() {
    this.roleId = this.$route.params.id;
    this.fetchMemberList(1);
  },

  methods: {
    fetchMemberList(isfirstPage = 0) {
      if (isfirstPage === 1) this.pagination.current = 1;
      this.loading = true;
      service.getMemberList(this.roleId, {
        offset: (this.pagination.current - 1) * this.pagination.pageSize,
        limit: this.pagination.pageSize,
        query: this.keyword
      }).then(data => {
        data.lists.map(item => {
          item.roles = [item.roleName];
          return item;
        });
        this.tableData = data.lists;
        this.pagination.total = data.sum || 0;
        this.loading = false;
      }).catch(e => {
        this.loading = false;
      });
    },
    pageChange(current) {
      this.pagination.current = current;
      this.fetchMemberList();
    },
    sizeChange(size) {
      this.pagination.pageSize = size;
      this.fetchMemberList(1);
    },
    removeMember() {
      if (this.selection.length) {
        const userId = this.selection.map(item => item.userId);
        this.$Modal.confirm({
          title: '警告',
          content: '是否删除该成员？删除后将无法恢复',
          onOk: () => {
            service.deleteMember(this.roleId, userId).then(data => {
              this.$Message.success('删除成员成功');
              this.fetchMemberList();
            });
          }
        });
      } else {
        this.$Message.warning(`请选择成员`);
      }
    },
    selectionChange(val) {
      this.selection = val;
    },
    addMember(val) {
      service.addMember(this.roleId, val).then(data => {
        this.$Message.success('添加成员成功');
        this.fetchMemberList(1);
      });
    }
  }
};
</script>



